<template>
  <div>
    <el-card header="敏感操作！" style="width:43%;margin:3rem auto;min-width:18rem" v-if="false">
      <el-form>
        <el-form-item label="请输入您的密码：">
          <el-input type="password" v-model="model.password" />
        </el-form-item>
        <el-form-item style="text-align:center;">
          <el-button type="primary">确认</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <el-form ref="form" :model="form" label-width="80px" @submit.native.prevent="handleSubmit">
      <el-form-item label="账户名">
        <h3>{{model.name}}123</h3>
      </el-form-item>
      <el-form-item label="真实姓名">
        <el-row>
          <el-input v-model="form.last_name" placeholder="姓" style="width:5rem;margin-right:1rem"></el-input>
          <el-input v-model="form.first_name" placeholder="名" style="width:10rem"></el-input>
        </el-row>
      </el-form-item>

      <el-form-item label="性别">
        <el-radio-group v-model="form.sex">
          <el-radio label="男"></el-radio>
          <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="头像">
        <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false"
          :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
          <img v-if="form.photo" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon" style="line-height: 178px;"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="出生日期">
        <el-date-picker v-model="form.born" type="date" placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submint">修改</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        model: {},
        form: {},
      }
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      handleSubmit(){},
    }
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;

    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>