<template>
    <div>
        <el-row>
            <el-button type="primary" round @click="handleCreate">新增学习项目</el-button>
        </el-row>
        <el-dialog title="新增学习项目" :visible.sync="centerDialogVisible" width="35%" center>
            <el-form>
                <el-form-item label="类别名称">
                    <el-input v-model="model.title" />
                </el-form-item>
                <el-form-item label="简介">
                    <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="model.content" />
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" native-type="submit" @click="handleSubmit">提 交</el-button>
            </span>
        </el-dialog>
        <el-table :data="tableData" style="width: 100%">
            <el-table-column label="创建日期" width="220">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.createdAt.substring(0,10) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="学习项目" width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>名称: {{ scope.row.title }}</p>
                        <p>简介: {{ scope.row.content }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.title }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index,scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                model: {},
                is_create: true,
                tableData: [],
                centerDialogVisible: false
            }
        },
        created() {
            this.init();
        },

        methods: {
            async init() {
                let res = await this.$http.get(`/tips`);
                this.tableData = res.data.tips;
            },

            async handleSubmit() {
                if (this.is_create) {
                    let res = await this.$http.post(`/tips`, this.model);
                    this.$message({
                        type: 'success',
                        message: '创建成功',
                    })
                    this.centerDialogVisible = false;
                    this.init();
                } else {
                    let res = await this.$http.put(`/tips/${this.model.id}`, this.model);
                    this.$message({
                        type: 'success',
                        message: '修改成功',
                    });
                    this.centerDialogVisible = false;
                    console.log(res)
                }
            },
            handleCreate() {
                this.centerDialogVisible = true;
                this.model = {};
                this.is_create = true;
            },
            handleEdit(row) {
                this.centerDialogVisible = true;
                this.model = row;
                this.is_create = false;
            },
            async handleDelete(index, row) {
                try {
                    await this.$confirm(`是否确定删除项目 "${row.title}"`, "提示", {
                        confirmButtonText: '走你',
                        cancleButtonText: '算了吧',
                        type: 'warning'
                    })
                    await this.$http.delete(`/tips/${row.id}`);
                    this.tableData.splice(index,1);
                    this.$message({
                        type: 'success',
                        message: '删除成功',
                    })
                } catch {
                    this.$message({
                        message: '取消删除',
                    })
                }
            }
        }
    }
</script>