<template>
    <div class="sign-in-box">
        <el-card header="Just管理系统" class="sign-in-card" style="text-align:center">
            <el-form class="sign-in-form" @submit.native.prevent="handleSignIn">
                <el-form-item label="管理员账号" class="sign-in-form-item">
                    <el-input v-model="model.username" />
                </el-form-item>
                <el-form-item label="管理员密码" class="sign-in-form-item">
                    <el-input type="password" v-model="model.password" />
                </el-form-item>
                <el-form-item class="sign-in-form-item" style="margin-top:4rem">
                    <el-button type="primary" native-type="submit">登录</el-button>
                    <el-button @click="toSignUp">注册</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                model: {},
            }
        },
        methods: {
            toSignUp() {
                this.$router.push('/sign-up')
            },
            async handleSignIn() {
                let res = await this.$http.post(`/admins/sign-in`, this.model);
                localStorage.token = res.data.token;
                this.$router.push({
                    name: 'main'
                });
                this.$message({
                    type: 'success',
                    message: '登录成功'
                })
            }
        }
    }
</script>

<style lang="scss">
    .sign-in-box {
        width: 45%;
        min-width: 35rem;
        margin: 6rem auto;

        .sign-in-card {
            width: 100%;

            .sign-in-form {
                width: 80%;
                margin: auto;

                .sign-in-form-item {
                    margin: 1rem;
                    text-align: center;
                }
            }
        }
    }
</style>