<template>
    <div>
        <el-row>
            <el-button type="primary" round @click="handleCreate">新增文章</el-button>
            <el-select v-model="model.TipId" style="margin-left:1rem;" placeholder="默认">
                <el-option v-for="item in lists" :key="item.id" :label="item.title" :value="item.id" />
            </el-select>
            <el-select v-model="model.TipId" style="margin-left:1rem;" placeholder="全部">
                <el-option v-for="item in lists" :key="item.id" :label="item.title" :value="item.id" />
            </el-select>

        </el-row>
        <el-table :data="tableData" style="width: 100%">
            <el-table-column label="创建日期" width="220">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.createdAt.substring(0,10) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="标题" width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>名称: {{ scope.row.title }}</p>
                        <p>简介: {{ scope.row.content }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.title }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="所属条目" width="220">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.Catalogue.title }}</span>
                </template>
            </el-table-column>
            <el-table-column label="所属类别" width="220">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.CatalogueId }}</span>
                </template>
            </el-table-column>

            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                model:{},
                tableData: [],
                lists:[],
                x:''
            }
        },
        created() {
            this.init();
        },
        methods: {
            async init() {
                let res = await this.$http.get(`/articles`);
                this.tableData = res.data.articles;

            },
            handleCreate() {
                this.$router.push(`/article/edit`)
            },
            handleEdit(index, row) {
                this.$router.push(`/article/edit/${row.id}`)
            },
            async handleDelete(index, row) {
                try {
                    await this.$confirm(`是否确定删除项目 "${row.title}"`, "提示", {
                        confirmButtonText: '走你',
                        cancleButtonText: '算了吧',
                        type: 'warning'
                    })
                    await this.$http.delete(`/articles/${row.id}`);
                    this.tableData.splice(index, 1);
                    this.$message({
                        type: 'success',
                        message: '删除成功',
                    })
                } catch {
                    this.$message({
                        message: '取消删除',
                    })
                }
            }
        }
    }
</script>