<template>
    <div>
        <h1>{{id ? '编辑':'新增'}}分类</h1>
        <el-form @submit.native.prevent="handleSubmit">
            <el-form-item label="标题">
                <el-input v-model="model.title" />
            </el-form-item>
            <el-form-item label="所属">
                <el-select v-model="x" placeholder="类别">
                    <el-option v-for="item in tipLists" :key="item.id" :label="item.title" :value="item.id">
                    </el-option>
                </el-select>
                <el-select v-model="model.CatalogueId" style="margin-left:1rem" placeholder="条目">
                    <el-option v-for="item in catalogueLists" :key="item.id" :label="item.title" :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <template><span>正文</span></template>
                <vue-editor v-model="model.content"></vue-editor>
            </el-form-item>
            <el-form-item>
                <el-row>
                    <el-button type="primary" native-type="submit">提交</el-button>
                    <el-button>重置</el-button>
                </el-row>
            </el-form-item>
        </el-form>
    </div>
</template>
<style>

</style>
<script>
    import {
        VueEditor
    } from 'vue2-editor'

    export default {
        props: {
            id: {},
        },
        components: {
            VueEditor,
        },
        data() {
            return {
                model: {},
                x:'',
                tipLists: [],
                catalogueLists: [],
            }
        },
        methods: {
            async init() {
                let res = await this.$http.get(`/articles/${this.id}`);
                this.model = res.data.article;

                let result = await this.$http.get(`/catalogues/${this.model.CatalogueId}`);
                this.x=result.data.catalogue.TipId
            },
            async getTips() {
                const res = await this.$http.get(`/tips`);
                this.tipLists = res.data.tips;
            },
            async getCatalogues() {
                let results = await this.$http.get(`/tips/${this.x}`);
                this.catalogueLists = results.data.tip.Catalogues;
                this.model.CatalogueId = this.catalogueLists[0].id;

            },
            async handleSubmit() {
                let res;
                if (this.id) {
                    res = await this.$http.put(`/articles/${this.id}`, this.model);
                    this.$router.push({
                        name: 'article-list'
                    });
                    this.$message({
                        type: 'success',
                        message: '修改成功',
                    })
                } else {
                    res = await this.$http.post(`/articles`, this.model);
                    this.$router.push({
                        name: 'article-list'
                    });
                    this.$message = ({
                        type: 'success',
                        message: '创建成功',
                    })
                }
            }
        },
        created() {
            this.getTips();
            this.id && this.init();
        },
        computed: {
            watchName() {
                return this.x;
            }
        },
        watch: {
            watchName(newVal, oldVal) {
                this.getCatalogues()
            }
        }
    }
</script>