import Vue from 'vue'
import Router from 'vue-router'
import Main from './views/Main.vue'
import SignIn from './views/SignIn.vue'
import SignUp from './views/SignUp.vue'

import ArticleList from './views/admin/article/ArticleList'
import ArticleEdit from './views/admin/article/ArticleEdit'
import TipList from './views/admin/tip/TipList'
import CatalogueList from './views/admin/catalogue/CatalogueList.vue'
import UserEdit from './views/admin/user/UserEdit'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/sign-in',
      name: 'sign-in',
      component: SignIn,
    },
    {
      path: '/sign-up',
      name: 'sign-up',
      component: SignUp,
    },
    {
      path: '/',
      name: 'main',
      component: Main,
      children:[

        {path:'/tip/list',name:'tip-list',component:TipList},
        {path:'/catalogue/list',name:'catalogue-list',component:CatalogueList},

        {path:'/article/list',name:'article-list',component:ArticleList},
        {path:'/article/edit/',name:'article-create',component:ArticleEdit},
        {path:'/article/edit/:id',name:'article-edit',component:ArticleEdit,props:true},

        // 管理员设置
        {path:'/user/setting',name:'user-setting',component:UserEdit},
      ]
    },
    ]
})
